.menu {
    display: flex;
    position: relative;
    &__block {
        display: flex;
        margin-left: 30px;
        padding-top: 10px;
    }
    &__item {
        display: flex;
        padding: 25px 50px 0 0;
        font-family: 'Open Sans', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        text-decoration: none;
        color: #4F4F4F;
        &::after {
            content: '';
            background-image: url('./arrow.svg');
            margin: 8px 0px 0px 28px;
            width: 6px;
            height: 10px;
            display: block;  
        }
        &:last-child:after {
            content: '';
            background-image: none;
            margin-left: 0;
        }
    }
}

.logo {
    display: block;
    position: absolute;
    left: -45px;
    height: 60px;
    width: 60px;
    margin-top: 20px;
    &__icon {
        width: 100%;
        height: 100%;
    }
}
