.game {
    padding: 85px 0 0 85px;
    &__item {
        list-style-type: none;
        font-family: 'Open Sans', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 150%;
        color: #000000;
        &::before {
            content: '';
            display: inline-block;
            margin-right: 12px;
            height: 12px;
            width: 12px;
            border-radius: 100%;
            background: #E082FD;
            box-shadow: 0px 2px 10px rgba(223, 128, 248, 0.54);
        }
    }
    &__input {
        margin-top: 17px;
        width: 522px;
        height: 64px;
        background: #FFFFFF;
        box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
        border: none;
        text-align: center;
        border-radius: 12px;
        outline: none;
        &::placeholder {
            text-align: left;
            padding-left: 43px;
            font-family: 'Ubuntu', sans-serif;
            font-style: normal;
            font-weight: 300;
            font-size: 18px;
            color: #000000;
            opacity: 0.5;
        }
    }
    &__btn {
        position: absolute;
        top: 42px;
        right: 120px;
        border: none;
        background-color: #FFFFFF;
        font-family: Ubuntu;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        color: #000000;
        opacity: 0.5;
        z-index: 99;
        cursor: pointer;
    }
    &__question {
        padding-top: 60px;
    }
    &__cube {
        padding-left: 160px;
    }
}
.form {
    position: relative;
}