.title{
    margin-top: 30px;
    min-height: 640px;
    max-width: 700px;
    margin-left: 25px;
    &__header {
        font-family: 'Montserrat', sans-serif;
        font-weight: bold;
        font-size: 92px;
    }
    &__descr {
        padding-top: 48px;
        font-family: 'Open Sans', sans-serif;
        font-style: normal;
        max-width: 460px;
        font-size: 18px;
        line-height: 30px;
        color: #343A45;
        &--top {
            font-weight: 600;
        }
        &--bottom {
            display: block;
            padding-top: 20px;
        }
    }
}