.chat {
    background: rgba(242, 244, 248, 0.6);
    border-radius: 40px;
    max-width: 510px;
    min-height: 245px;
}
.noll {
    display: flex;
    padding: 30px 0 0 85px;
    max-width: 485px;
    justify-content: space-between;
    &__name {
        font-family: 'Montserrat', sans-serif;
        font-weight: normal;
        font-size: 18px;
        color: #989EAA;
    }
    &--delite {
        width: 12px;
        height: 12px;
        cursor: pointer;
        & span {
            display: block;
            width: 17px;
            height: 2px;
            background-color: #989EAA;
            &:nth-child(1) {
                transform: translateY(3px) rotate(-45deg)
            }
            &:nth-child(2) {
                transform: translateY(1px) rotate(45deg)
            }
        }
        &:hover {
            transition: 1.2s all;
            transform: rotate(360deg);
        }
    }
}
.assistant {
    display: flex;
    &__text {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 30px 10px 0 0;
        max-width: 310px;
        max-height: 119px;
        background: #FFFFFF;
        box-shadow: 8px 24px 40px rgba(123, 127, 132, 0.2);
        border-radius: 40px;
        & p {
            text-align: center;
            font-family: 'Open Sans', sans-serif;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 30px;
            & span {
                font-weight: bold;
                color: #3C3D46;
            }
        }
    }
    &__img {
        cursor: pointer;
    }
}