.panel {
    display: block;
    position: relative;
    width: 710px;
    height: 881px;
    background: url('../work-panel/background.png') center center/cover no-repeat;
    &__arrow {
        position: relative;
        width: 138px;
        height: 138px;
        background: url('../work-panel/arrow/arrow-box.png') center center/cover no-repeat;
        &__control {
            position: absolute;
            width: 23px;
            height: 23px;
            cursor: pointer;
            border: none;
            &--up {
                left: 57px;
                top: 11px;
                background: url('../work-panel/arrow/arrow-up.svg') center center/cover no-repeat;
            }
            &--down {
                left: 57px;
                bottom: 11px;
                background: url('../work-panel/arrow/arrow-down.svg') center center/cover no-repeat;
            }
            &--left {
                left: 12px;
                bottom: 56px;
                background: url('../work-panel/arrow/arrow-left.svg') center center/cover no-repeat;
            }
            &--right {
                right: 12px;
                bottom: 56px;
                background: url('../work-panel/arrow/arrow-right.svg') center center/cover no-repeat;
            }
        }
    }
    &__button {
        display: flex;
        justify-content: space-around;
        width: 194px;
        height: 90px;
        background: #FFFFFF;
        box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 38.5px;
        transform: rotate(-18.44deg);
        &__control {
            margin-top: 5px;
            width: 75px;
            height: 75px;
            background: #FFFFFF;
            border: none;
            border-radius: 100%;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            outline: none;
            cursor: pointer;
            font-family: 'Ubuntu', sans-serif;
            font-style: normal;
            font-weight: normal;
            font-size: 24px;
            color: #DEDEDE;
        }
    }
      
}
.control__block {
    display: flex;
    justify-content: space-between;
    position: absolute;
    width: 460px;
    max-width: 460px;
    bottom: 120px;
    left: 130px;
}
.info {
    position: absolute;
    bottom: 350px;
    left: 235px;
    width: 237px;
    max-width: 237px;
    & span {
        font-family: 'Open Sans', sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 19px;
        color: #C0C0C0;
    }
}
